.brand-side-logo {
    position: relative;
    margin-right: -74px;

    img {
        width: 100%;
    }
}
.brand-cont {
    margin: 78px 0 80px;
    .visual-banner {
        overflow: hidden;
    }
    .slide-list {
        width: 100%;
        overflow: hidden;
    }
    .square-banner .txt-box .tit {
        margin-bottom: 10px;
    }
    .thumb-wrap {
        margin-top: 80px;
        .tit {
            margin-bottom: 40px;
            font-weight: bold;
            text-align: center;
            font-size: 15px;
            color: #0f0f10;
        }
        .thumb-list {
            margin-left: -15px;
            &::after {
                content: '';
                display: table;
                clear: both;
            }
            .item {
                position: relative;
                float: left;
                width: 390px;
                margin-left: 15px;
                margin-bottom: 15px;
            }
            &.slide-list {
                margin: 0;
                .item {
                    margin-left: 0;
                    margin-bottom: 0;
                }
            }
        }
    }
    .brand-info {
        .info {
            margin-top: 30px;
            margin-bottom: 50px;
            &::after {
                content: '';
                display: table;
                clear: both;
            }
            .tit {
                float: left;
                width: 430px;
                font-size: 30px;
                font-weight: bold;
                line-height: 1.2;
                color: #1f1f1f;
                letter-spacing: -1px;
            }
            .txt {
                float: right;
                width: 460px;
                margin-right: 30px;
                font-size: 14px;
                color: #1f1f1f;
                line-height: 1.6;
                p {
                    font-size: 14px;
                    margin-bottom: 20px;
                }
            }
        }
        .collection {
            padding-top: 40px;
            border-top: 4px solid #1f1f1f;
            .tit {
                margin-bottom: 50px;
                font-weight: bold;
                font-size: 15px;
                color: #0f0f10;
            }
            .collection-link {
                display: inline-block;
                padding: 10px;
            }
            .collection-list {
                &::after {
                    content: '';
                    display: table;
                    clear: both;
                }
                .item {
                    position: relative;
                    float: left;
                    width: 294px;
                    height: 382px;
                    background-size: cover;
                    background-position: center;
                    img {
                        position: absolute;
                        right: 24px;
                        top: 102px;
                        width: 235px;
                        height: 296px;
                    }
                }
            }
        }
    }
}
.content-cont.special-cont .category-menu {
    margin-top: 20px;
}
